* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #333;
  color: #fff;
}

button {
  padding: 5px 10px;
  background: #111;
  color: #fff;
  border: solid 3px #fff;
  border-radius: 5px;
}
button:hover {
  cursor: pointer;
}

/* Navigation */
nav {
  background: #111;
}

.nav-container {
  min-height: 51px;
  margin: 0 auto;
  width: 85%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* URL Form */
#URLForm input {
  min-width: 350px;
  min-height: 30px;
  border-radius: 8px;
  margin-right: 20px;
  padding: 0 8px;
}

/* Navigation List */
ul {
  display: flex;
  justify-content: space-evenly;
}

ul li {
  margin: 0 10%;
  list-style-type: none;
}

a {
  color: #fff;
  text-decoration: none;
}

a[href='library.html'] {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Container */
.container {
  display: flex;
  width: 95%;
  margin: 0 auto;
  align-items: stretch;
}

.main {
  flex: 7;
}

.videos-list {
  flex: 3;
}

/* Left Section */

.main {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0 20px 0 35px;
}

.iframe-container {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
  margin-top: 20px;
}

.iframe-container iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

#translater {
  margin: 20px 0;
  display: flex;
  justify-content: space-evenly;
}

#translationInput {
  flex: 2;
}

#buttons {
  flex: 1;
}

#translated {
  flex: 2;
}

#buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}
#buttons button {
  padding: 5px 10px;
  margin: 5px 0;
  min-width: 80px;
  width: 100%;
}

/* Right Section */
.videos-list {
  /* padding: 10px 50px 10px 10px; */
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#right-form {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.subtitles {
  display: flex;
  align-items: center;
}

.subtitles input[type='checkbox'] {
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 8px;
}

.subtitles label[for='subtitles'] {
  font-size: 1.5rem;
}

#right-form input[type='text'] {
  min-width: 150px;
  min-height: 30px;
  border-radius: 8px;
  margin: 0 15px;
  padding: 0 8px;
}

#right-form input[type='submit'] {
  padding: 5px 10px;
  background: #111;
  color: #fff;
  border: solid 3px #fff;
  border-radius: 5px;
}

/* Fetched Data (Videos from YouTube) */

.fetched-videos {
  max-width: 400px;
}

#title {
  padding: 10px;
  margin-top: 10px;
  background: #444;
}

.vidosiky {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
}

.vidosiky iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.right-buttons {
  display: flex;
}

.right-buttons button {
  flex: 1;
  background: lightgrey;
  color: #000;
  border-radius: 0;
  border: solid 1px #000;
}

.right-buttons button:hover {
  cursor: pointer;
  background: #f0f8ff;
}

/* ****************** */
/* Library CSS */
/* ****************** */

.watch-later {
  margin-left: 50px;
  margin-top: 30px;
}

hr {
  /* color: gold; */
  background: goldenrod;
  width: 100%;
  height: 4px;
  border: none;
}

.watch-later-vidos {
  margin: 20px;
}
